#root {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.App {
	width: 800px;
}

.post {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 15px;
	margin-top: 15px;
	border: 2px solid rgb(8, 147, 172);
}

.post__btns {
	display: flex;
}


/* При добалении элемента. Когда анимация только началась */
.post-enter {
	transform: translateX(-350px);
}

/* При добалении элемента. Когда анимация активна */
.post-enter-active {
	transform: translateX(0px);
	transition: all 500ms ease-in;
}


/* При удалении элемента. Когда анимация только началась */
.post-exit {
	opacity: 1;
}

/* При удалении элемента. Когда анимация активна */
.post-exit-active {
	transform: translateX(-350px);
	transition: all 500ms ease-in;
}

.page__wrapper {
	margin-top: 30px;
	display: flex;
}

.page {
	border: 1px solid teal;
	padding: 10px;
	cursor: pointer;
}

.page__current {
	border: 2px solid orange;
	font-weight: bold;
}

.navbar {
	height: 50px;
	width: 100%;
	padding: 0 15px;
	display: flex;
	align-items: center;
	background: lightcyan;
}

.navbar__links {
	margin-left: auto;
}